import React from "react"
import Layout from "../components/layout"
import Links from "../components/links"
import "../styles/about.scss"

export default () => (
  <Layout>
    <div id="about">
    <img src="https://shuttlefrog.weebly.com/uploads/2/8/6/2/28624773/published/img-9514.jpg?1525277055" alt="Me"></img>
    <div className="description">
      <p>Hey there, I'm a recent graduate at <b>University of California, Santa Cruz</b> with a double major in Computer Science B.S. and Computer Game Design B.S. I use to work part time at <b>LEEPS Lab</b>, programming interactive web applications for econ experiments that dealt with game theory and discrete choices. Now I'm looking to get a full time job in web development.
      <br></br><br></br>
      "Computer Science? Then why do you have an art portfolio?" Because alongside my interest in programming and web development, I've kept a love for the arts since I was a fetus! Game design became a passion hobby that allows me to dual-wield both passions. So currently, I'm the Co-producer and Art Director for the game, <b>SAVOR SABER</b>.
      <br></br><br></br>
      Aside from drawing and coding, I enjoy watching performing arts, traveling, and learning new languages!
      <br></br><br></br>
      Thanks for taking your time to check this out!
      </p>
      <Links color="#ccc"></Links>
      </div>
    </div>
  </Layout>
)